<template>
  <div v-if="items">
  <div class="container">

      <div class="col-lg-2">
        <span class="btn" @click="goBack">Gå Tilbage</span>
       </div>
      <h1>Checkout</h1>
      
      <div class="row">
        <div class="col">
          <img class="card-img-top" :src="items.img" alt="Card image cap" style="height: 100px; width: auto; ">
          </div>
        <div class="col">

          Title:  {{items.title}}
        </div>
      <div class="col">
        Farve: {{items.color}}
      </div>
       <div class="col">
         Størrelse: {{items.size}}
       </div>
        <div class="col">
             antal: {{items.quantity}}
        </div>
        <div class="col">
          pris: {{items.price}}
        </div>
        <div class="col">
          total: {{items.total}}
        </div>
       
      </div>
    </div>
    <div class="row justify-content-center">
      <div class="col-4">
      <button class="btn" @click="handleSubmit">
        Køb
      </button>
      </div>
      <div v-if="warring">noget gik glat</div>
    </div>
  </div>
  <div v-else class="container">
  <div class="col-lg-2">
      <router-link :to="{name: 'HomePage'}">
              <span class="btn btn-primary">Gå Tilbage</span>
    </router-link>
  </div>
    <div>
    <h1>du har intet i indskøbskurv</h1>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
import useCollectionitem from '../composables/useCollectionItem'
import { useRouter } from 'vue-router'
export default {

  
  setup(){
     let items = null
     const {error , additem , isPending} = useCollectionitem("order")
      let warring = ref(false)
      const router = useRouter()
      if(localStorage.getItem("order")){
        items = JSON.parse(localStorage.getItem("order"))
      }

     const handleSubmit = async () => {
        if(items){
        await additem(items)
        router.push({name: 'Thanks'})
        } else {
         router.push({name: 'HomePage'})
        }
      }

       const goBack = () =>{
          router.go(-1)
      }

    return {items,additem,error,isPending,handleSubmit,warring, goBack}
  }
}
</script>

<style lang="scss">
@import "../assets/styles/_shared.scss"


</style>