<template>
    <h1>items</h1>
    <div class="row" v-if="documents">
        <!-- t-shirt -->
        <div class="card" style="width: 18rem;" v-for="doc in formattedDocuments" :key="doc.id" >
        <!-- <img class="card-img-top" alt="Card image cap"> -->
            <div class="card-body">
                <h5 class="card-title">{{doc.title}}</h5>
                 <img class="card-img-top" :src="doc.img" alt="Card image cap" style="height: 250px">
                <p class="card-text">{{doc.appertize}} </p>         
                <div :key="doc.id">
                <router-link :to="{name: 'Details', params: {id: doc.id}}">
                <span class="btn btn-primary">Gå til {{doc.title}}</span>
                </router-link>
                </div>
           
            </div>
    
    </div>

</div>
</template>

<script>

import getCollectionitem from '../composables/getCollectionItem.js'
import {computed} from 'vue'
export default {
       setup(){
            const {documents,error} = getCollectionitem('Items')
            const formattedDocuments = computed(() => {
            if(documents.value){
                
                return documents.value.map(doc => {
                    return {...doc}
                })
            }
            })
           return {
            error,
            documents,
            formattedDocuments
            }
       }
}
</script>

