<template>
        <div v-if="error">
                    {{ error }}
        </div>
              <div v-if="documents" class="messages" ref="messages">
                 <table class="table">
                    <thead class="thead-dark">
                        <tr>
                        <th scope="col">#</th>
                        
                        <th scope="col">doneornot</th>
                        <th scope="col">img</th>
                        <th scope="col">title</th>
                        <th scope="col">size</th>
                        <th scope="col">color</th>
                        <th scope="col">quantity</th>
                        <th scope="col">price</th>
                        <th scope="col">total</th>
                        
                        </tr>
                    </thead>
                    <tbody div v-for="doc in formattedDocuments" :key="doc.id" class="single">
                    <tr>
                    <th scope="row">{{doc.id}}</th>
                    <td span @click="handleComplete(doc.id)" class="material-icons tick">done</td>
                    <td><img :src="doc.img" alt="Imgs" style="height: 50px"></td>
                    
                    <td>{{doc.title}}</td>
                      <td>{{doc.size}}</td>
                    <td>{{doc.color}}</td>
                    <td>{{doc.quantity}}</td>
                    <td>{{doc.price}}</td>
                    <td>{{doc.total}}</td>
                    </tr>   
            </tbody>
       </table>
       </div>
</template>

<script>
import {computed} from 'vue'

export default {
    props:['error','documents'],
    setup(props,context){ 
       const formattedDocuments = computed(() => {
            if(props.documents){
                return props.documents.map(doc => {
                    let time = doc.createdAt
                    return {...doc, createdAt: time}
                })
               
            }
        })

         const handleComplete = (id) => {
             
               let p = props.documents.find((item) => {
                    return item.id === id
                    })
            console.log(p.incomplete)
              p.incomplete = !p.incomplete

            }
        return {formattedDocuments,handleComplete}
    }
}
</script>

<style>
.material-icons {
    font-size: 24px;
    margin-left: 10px;
    cursor: pointer;
}
.material-icons:hover{
    color: rgb(108, 108, 224);
}
.complete{
    border-left: 4px solid rgb(44, 167, 44);
}
.complete .tick{
   color: rgb(44, 167, 44);
}
</style>