<template>
    
    <div v-if="item" class="container details">
      <div class="col-lg-2">
          <router-link :to="{name: 'HomePage'}">
                <span class="btn btn-primary">Gå Tilbage</span>
         </router-link>
      </div>
      <h1>{{item.title}}</h1>
            <div class="row">
                    <div class="col text">
                      {{item.text}}
                    </div>
              <div class="col-md-3  imgphoto">
                  <img class="card-img-top" :src="item.img" alt="Card image cap">
              </div>
              <div class="col-md-3">
                <div class="text">
                  <div>
                    <h4>
                      Produktet 
                    </h4>
                    <div class="price">
                      Pris {{item.price}}<span v-if="item.title === 'T-shirt'"> inkl. trykomkostninger</span> excl fragt
                    </div>
                  </div>
                  </div>
                      <form @submit.prevent="handleSubmit(item)">
                        <div>
                          <div>
                           Størrelse:  <select class="dropdown" v-model="size">
                              <option v-for="s in item.size" :value="s" :key="s">
                                {{s}}
                              </option>
                          </select>
                          </div>
                             <div>
                               Farve: 
                            <select class="dropdown" v-model="color">
                              <option v-for="c in item.color" :value="c" :key="c">
                                {{c}}
                              </option>
                          </select>
                          </div>
                           <div>
                          Antal: 
                          <select class="dropdown" v-model="quantity">
                              <option v-for="q in quantityarray" :value="q" :key="q">
                                {{q}}
                              </option>
                          </select>
                          </div>
                          <div class="details__button">
                            <button class="btn btn-dark">Læg i indskøbskurv</button>
                          </div>
                        </div>
                      </form>
                       <div class="details__button">
                      <router-link :to="{name: 'Checkout'}">
                         <span class="btn btn-dark">Checkout</span>
                      </router-link>
                  </div>
                </div>

          </div>
        </div>
      <div v-else>
        <h1>Waiting...</h1>
      </div>
</template>

<script>

import {ref} from 'vue'

import getItem from '../composables/getItem'
import {timestamp} from '../firebase/config'
export default {
  props:['id'],
  setup(props){
    const {item, error ,load } = getItem('Items', props.id)
     let size = ref('S')
     let color = ref('Hvid')
     let quantity = ref(1)
     const quantityarray = [1,2,3,4,5,6]
      load()

    const handleSubmit = (item) => {
      const pris = item.price * quantity.value
      const order = {
        title: item.title,
        incomplete: false,
        total: pris,
        price: item.price,
        img: item.img,
        size: size.value,
        color: color.value,
        quantity: quantity.value,
        createdAt: timestamp()
        }
      localStorage.setItem('order' ,JSON.stringify(order))
      } 
      
     if(localStorage.getItem('order')){
         const hello = localStorage.getItem('order');
      console.log(hello)
     }
    return {error,item, size,color,handleSubmit,quantityarray,quantity}
  }  
}
  


</script>

<style>

.details_right_text {
    margin-left: 8.33333%;
}

.details__button{
  margin: 5px;
}

.imgphoto {
    padding-left: 8px;
    padding-right: 8px;
}

.details{
    width: 300px;
    margin: 20px auto;
}

.text{
  text-align: left;
}
select{
  margin: 4px;
}

</style>