<template>
<div class="contianer">
<h1>Admin</h1> 
      <div class="row">
      <button @click="show = 'overall'">Overall</button>
      <!-- <button @click="show = 'add'">add</button> -->
      <button  @click="show = 'edit'">edit</button>
      </div>
      <div v-if="show === 'overall'">
    <ViewOrders :documents="documents" :error="error"/>
      </div>
     
      <div v-if="show === 'edit'">
        <h1>Edit</h1>
      </div>
  </div>  
</template>

<script>
import { ref, watch } from 'vue'
import ViewOrders from "../compoentsAdmin/ViewOrders"
import getCollectionItem from '../composables/getCollectionItem'
import { useRouter } from 'vue-router'
import getUser from '../composables/getUser'

export default {
  components:{
    ViewOrders
  },
        setup(){
            const {user } = getUser()

            const router = useRouter()
            watch(user, () => {
              if(!user.value){
                router.push({name: 'HomePage'})
              }
            })
          
          const show = ref("overall")
          const{error, documents} = getCollectionItem('order')
        return{error,documents,show}
    }
}
</script>

