<template>
  <nav class="navbar navbar-light bg-light">
     <div class="container-fluid">
       <router-link :to="{name: 'HomePage'}">
      <h2 class="navbar-brand"><span class="logo">Real Man</span></h2>
       </router-link>
       <div v-if="user" class="row">
           <button @click="handleClick" class="logout">Logout</button>
       <router-link :to="{name: 'Admin'}">
          <button class="submit buttongreen">Admin</button>
       </router-link>
         <h2>Hej: {{user.email}}</h2>
       </div>
       <div v-else>
        <form @submit.prevent="handleSubmit" class="d-flex">
            <input type="email" class="form-control me-2" required placeholder="email" v-model="email">
            <input type="password" class="form-control me-2" required ="password" placeholder="password" v-model="password">
            <button class="submit buttongreen">Login</button>
        </form>  
      </div>
    </div>
  </nav>
   <div class="error" v-if="error">{{ error }}</div>
  <router-view/>
  <div id="footer">

  </div>
</template>
<script>
import { ref } from 'vue'
import useLogin from './composables/useLogin.js'
import useLogout from './composables/useLogout.js'
import getUser from './composables/getUser.js'
// import { watch } from 'vue'
// import { useRouter } from 'vue-router'
export default {
 setup(){
   const email = ref('')
   const password = ref('')
   const {user} = getUser()
  const {logout } = useLogout()
  let checklogin = ref(false)
  const {error , login} = useLogin()
      const handleSubmit = async () => { 
        await login(email.value,password.value)
          if(!error.value){
               console.log("check login", checklogin)
        }
      }
    const handleClick = async () => {
      await logout()
        if (!error.value) {
        console.log('user logged out')
      }
    }
     
   return {email , password,handleSubmit, error,checklogin,user,handleClick}
   }
}
</script>
